import * as React from "react";
import Description from "./components/Description";

class Root extends React.Component {
  public render() {
    return (
      <main className="main main--Root">
        <p>
          Welcome to the wonderful world of Pokmon! This site has everything
          you need to know about these creatures called Pokmon.
        </p>

        <Description
          title="Pokmon"
          description="Pokmon are fantastic creatures with special powers and
             abilities. Some people keep Pokmon as pets, and others battle them
             to become Pokmon masters."
          page="pokemon"
        />

        <Description
          title="Berries"
          description="Berries are snacks for Pokmon. These berries will have various
            effects like healing the Pokmon, curing them of status ailments, or
            boosting their abilities!"
          page="berry"
        />

        <Description
          title="Machines"
          description="Technical Machines (TMs) and Hidden Machines (HMs) are machines that
          are used to teach Pokmon new abilities or 'moves'. These moves can
          be used in battle or in the outside world to aid your journey."
          page="machine"
        />
      </main>
    );
  }
}

export default Root;



